<template>
  <div class="m-group">
    <h2 class="c-sidebar-right-title m-title">
      <svg height="512" viewBox="0 0 512 512" width="512" xmlns="http://www.w3.org/2000/svg" v-bind:class="'u-icon'" v-bind:svg-inline="''" v-bind:role="'presentation'" v-bind:focusable="'false'" v-bind:tabindex="'-1'"><path d="M256 256h85.333v21.333C341.333 300.897 360.436 320 384 320s42.667-19.103 42.667-42.667V256H512V42.667C512 19.103 492.897 0 469.333 0H256z" fill="#dd636e"/><path d="M469.333 0H384v320c23.564 0 42.667-19.103 42.667-42.667V256H512V42.667C512 19.103 492.897 0 469.333 0z" fill="#d82e3d"/><path d="M256 256v-85.333h21.333C300.897 170.667 320 151.564 320 128s-19.103-42.667-42.667-42.667H256V0H42.667C19.103 0 0 19.103 0 42.667V256z" fill="#ffe646"/><path d="M277.333 85.333H256V0H128v256h128v-85.333h21.333C300.898 170.667 320 151.564 320 128s-19.102-42.667-42.667-42.667z" fill="#ffc81e"/><path d="M256 256h-85.333v-21.333C170.667 211.102 151.564 192 128 192s-42.667 19.102-42.667 42.667V256H0v213.333C0 492.897 19.103 512 42.667 512H256z" fill="#d49eee"/><path d="M256 512V256h-85.333v-21.333C170.667 211.103 151.564 192 128 192v320z" fill="#b77ede"/><path d="M256 256v85.333h-21.333C211.102 341.333 192 360.436 192 384s19.102 42.667 42.667 42.667H256V512h213.333C492.897 512 512 492.897 512 469.333V256z" fill="#badc1e"/><path d="M384 512h85.333C492.897 512 512 492.897 512 469.333V256H384z" fill="#73be14"/><path d="M341.333 249v28.333C341.333 300.897 360.436 320 384 320s42.667-19.103 42.667-42.667V249z" fill="#dd636e"/><path d="M384 246v74c23.564 0 42.667-19.103 42.667-42.667V246z" fill="#d82e3d"/></svg>
      <span class="u-text">各职业门派群</span>
      <a
          class="u-show-message"
          target="_blank"
          href="https://www.jx3box.com/tool/13659/"
      >申请展示</a
      >
    </h2>
    <el-collapse accordion v-if="groups && JSON.stringify(groups) !== '{}'">
      <el-collapse-item
          v-for="(school, key) in groups"
          :title="key"
          :key="key"
          :name="key"
      >
        <ul class="u-groups">
          <li v-for="(group, k) in school" :key="k">
            <el-popover
                placement="left-start"
                trigger="hover"
                :content="group.tags"
                :disabled="group.tags.split(',').length <= 3">
              <div slot="reference">
                <el-tag
                    class="u-kungfu"
                    v-if="group.kungfu"
                    size="mini"
                    effect="dark"
                    :style="{backgroundColor: kungfu_color(group.kungfu)}"
                    v-text="`${group.kungfu}`"
                ></el-tag>
                <template v-for="(tag, i) in group.tags.split(',')">
                  <el-tag
                      class="u-tags"
                      v-if="tag && i < 3"
                      size="mini"
                      effect="plain"
                      :key="i"
                      v-text="tag"
                  ></el-tag>
                </template>
                <div style="height:3px" v-if="group.tags"></div>
                <el-tag
                    class="u-platform"
                    v-if="group.platform == 'QQ'"
                    size="mini"
                    v-text="`${group.platform}群`"
                ></el-tag>
                <el-tag
                    class="u-platform"
                    v-if="group.platform == 'YY'"
                    size="mini"
                    type="info"
                    v-text="`${group.platform}群`"
                ></el-tag>
                <el-tag
                    class="u-number"
                    size="mini"
                    v-clipboard:copy="group.number"
                    v-clipboard:success="copy_success"
                    v-clipboard:error="copy_error"
                >
                  <i class="el-icon-document-copy"></i>
                  <span v-text="' ' + group.number"></span>
                </el-tag>
              </div>
            </el-popover>
          </li>
        </ul>
      </el-collapse-item>
    </el-collapse>
    <div v-else class="u-groups-empty">暂无相关群组</div>
  </div>
</template>

<script>
  import {get_groups} from '../service/group';
  import xfmap from "@jx3box/jx3box-data/data/xf/xf.json";

  export default {
    name: "group",
    data() {
      return {
        groups: null,
      };
    },
    methods: {
      copy_success() {
        this.$notify({title: "复制成功", type: "success"});
      },
      copy_error() {
        this.$notify({title: "浏览器不支持", type: "error"});
      },
      kungfu_color(val) {
        return xfmap[val] ? xfmap[val]["color"] : '';
      }
    },
    mounted: function () {
      // 获取群组
      get_groups('bps', {order_by: 'school', group_by: 'school'}).then(
        (data) => {
          data = data.data;
          if (data.code === 200) this.groups = data.data.groups;
        },
        () => {
          this.groups = false;
        }
      )
    },
  };
</script>

<style lang="less">
  @import "../assets/css/group.less";
</style>
