<template>
    <div
        id="app"
        :class="{ 'p-list': mode == 'list', 'p-single': mode == 'single' }"
    >
        <Header></Header>
        <Breadcrumb
            name="职业专栏"
            slug="bps"
            root="/bps"
            :publishEnable="true"
            :adminEnable="true"
            :feedbackEnable="true"
        >
            <svg xmlns="http://www.w3.org/2000/svg" width="256" height="256" viewBox="0 0 256 256" shape-rendering="geometricPrecision" text-rendering="geometricPrecision" image-rendering="optimizeQuality" fill-rule="evenodd" clip-rule="evenodd" v-bind:slot="'logo'" v-bind:svg-inline="''" v-bind:role="'presentation'" v-bind:focusable="'false'" v-bind:tabindex="'-1'"><path d="M62.239 5.018h167.362V250.98c-55.785.005-111.575 0-167.362 0-19.794 0-35.84-16.047-35.84-35.841V40.858c0-19.793 16.046-35.84 35.84-35.84zm55.634 14.336h97.392v159.945H62.239a35.67 35.67 0 00-21.504 7.167V40.858c0-11.876 9.628-21.504 21.504-21.504h7.168v62.919c0 4.013 3.13 7.262 7.168 7.262a7.157 7.157 0 005.068-2.099L93.64 75.439l11.997 11.997a7.168 7.168 0 0012.236-5.069V19.354zm-14.336 0v45.708l-9.897-9.897-9.897 9.897V19.354h19.794zM62.239 222.308a7.168 7.168 0 01-7.168-7.169 7.168 7.168 0 017.168-7.168h153.026v-14.336H62.239c-11.876 0-21.504 9.628-21.504 21.504 0 11.877 9.628 21.505 21.504 21.505h153.026v-14.336H62.239z"/></svg>
            <Info />
        </Breadcrumb>
        <LeftSidebar>
            <Nav />
        </LeftSidebar>
        <Main :withoutRight="false">
            <single v-if="mode == 'single'" />
            <div class="m-main" v-else>
                <tabs />
                <router-view />
            </div>
            <RightSidebar>
                <Extend />
            </RightSidebar>
            <Footer></Footer>
        </Main>
    </div>
</template>

<script>
import Info from "@/components/Info.vue";
import Nav from "@/components/Nav.vue";
import Extend from "@/components/Extend.vue";
import tabs from "@/components/tabs";
import single from "@/components/single.vue";
import {getPID,getAppID,getQuery,getAppType} from '@jx3box/jx3box-common/js/utils'
import {__Root} from '@jx3box/jx3box-common/data/jx3box.json'

export default {
    name: "App",
    props: [],
    data: function() {
        return {};
    },
    computed: {
        mode: function() {
            return this.$store.state.mode;
        },
    },
    methods: {},
    beforeCreate: function() {
        let id = getAppID()
        let pid = getPID()

        // 旧单页链接跳转
        if(!id && pid){
            let type = getAppType()
            let test = __Root + type + '/' + pid
            location.href = __Root + type + '/' + pid
        }

        // 处理模式 & 文章ID
        this.$store.state.mode = id ? 'single' : 'list'
        this.$store.state.pid = id

        // 捕获subtype
        if(this.$store.state.mode == 'list'){
            this.$store.state.subtype = getQuery("subtype");
        }
    },
    components: {
        Info,
        Nav,
        Extend,
        tabs,
        single,
    },
};
</script>

<style lang="less">
